@import '../../../../App.scss';

.postContent {
    border: 0.5px solid var(--quinary-color);
    border-radius: 25px;
    overflow: hidden;
}

.action {
    margin-top: 15px;
}

p {
    margin: 0;
}

.label {
    color: var(--tertiary-color);
    margin-bottom: 15px;
}

.profile {
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--secondary-color);
    box-shadow: 0px 4px 4px 0px rgba(30, 30, 30, 0.15);
    background-color: var(--secondary-color);
    padding: 0 15px;
    border-bottom: 0.5px solid var(--quinary-color);
    position: sticky;
}

.detail {
    display: flex;
    align-items: center;
    gap: 10px;
}

.three_dots {
    cursor: pointer;
}

.profileIcon {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
    object-fit: cover;
}

.name {
    color: var(--tertiary-color);
}

.postCell {
    background-color: var(--senary-color);
    padding: 15px;
}

.option {
    color: var(--tertiary-color);
}

.owner-profile {
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 15px 0;
}

.owner-profile>img {
    border-radius: 50px;
    object-fit: cover;
}

.post-image>img {
    border-radius: 25px;
    object-fit: cover;
}

.details {
    flex-grow: 1;

    overflow: {
        x: hidden;
        y: auto;
    }

    ;
}

.sticky_content {
    position: sticky;
    top: 0;
    background-color: var(--senary-color);
    z-index: 999;
    margin-bottom: 15px;
    // border-bottom: solid 1px var(--quinary-color);
}

.count {
    color: var(--quinary-color);
}

.post-image {
    display: flex;
    gap: 15px;
    height: auto;
    overflow: auto;
}

.not_card {
    max-height: 320px;
}

.card_height {
    max-height: 385px;
}

.details>.description {
    flex-grow: 1;
}

.post-info>.info:first-child>img {
    margin-bottom: 5px;
    object-fit: cover;
}

.info {
    display: flex;
    align-items: center;
    gap: 5px;
    width: max-content;
}

.action-option {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.option-container {
    display: flex;
    align-items: center;
    gap: 10px;
}

.warning-input {
    margin-top: 15px;
}

.input-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}

.inputBox {
    height: 20px;
    border: 1px solid var(--quinary-color);
    border-radius: 25px;
    flex-grow: 1;
    padding: 10px 20px 10px 50px;
    line-height: 18px;
    color: var(--tertiary-color);
    font-size: var(--heading3);
    overflow: hidden;
    resize: none;
    margin-bottom: 2px;
}

.inputBox::placeholder {
    font-size: var(--heading3);
    color: var(--quinary-color);
    line-height: 18px;
    padding-top: 2px;
}

.input-label {
    margin-bottom: 5px;
}

.smiley {
    position: absolute;
    left: 20px;
}

.arrow {
    padding-right: 20px;
}

.radio {
    margin: 0 !important;

    &>.MuiFormControlLabel-label {
        @extend .regular-16;
    }
}

.info_text {
    text-align: center;
}

.error_text {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.not_a_card {
    background-color: transparent !important;
    box-shadow: none !important;
}

.card {
    border-radius: 25px !important;
    border: solid 1px var(--quinary-color);
    padding: 5px;
    height: fit-content;
}

.comments_viewer_flag {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.report_detail_container {
    gap: 10px;
    display: grid;
}

.report_attachment_container {
    display: grid;
    gap: 11px;
}