.width-50 {
  width: 50%;
}

.width-50>.button-container {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.report-layout {
  display: grid;
  grid-template-rows: 50px;
  height: 100%;
  gap: 15px;
}

.content {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 320px auto auto auto auto;
  overflow: hidden;
}

.content>.report-list {
  grid-column: span 1;
}

.content>.reportView {
  grid-column-start: 2;
  grid-column: span 4;
  margin-left: 15px;
  overflow: auto;
}



// Alert CSS
.message {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: fit-content;
}

.show {
  animation: slideIn 0.5s ease forwards;
}

.hide {
  animation: slideOut 0.5s ease forwards;
}

@keyframes slideIn {
  from {
    bottom: 0;
    /* Start position */
    opacity: 0;
  }

  to {
    bottom: 20px;
    /* End position */
    opacity: 1;
  }
}

@keyframes slideOut {
  from {
    bottom: 20px;
    /* Start position */
    opacity: 1;
  }

  to {
    bottom: 0;
    /* End position */
    opacity: 0;
  }
}

.submit_button_container {
  display: flex;
  justify-content: flex-end;
}

.submit_button {
  width: 220px;
}

.image-gallery img {
  width: 80px;
  height: 80px;
}