@import '../../../../App.scss';

.comment {
    &_container {
        display: flex;
        gap: 10px;
        flex-direction: column;

        &:not(:last-child) {
            .info_pic {
                margin-bottom: 15px;
            }
        }
    }

    &_user_name {
        width: fit-content;
    }

}

.info {
    &_pic {
        display: flex;
        gap: 10px;
        ;
    }
}

.likes {
    display: flex;
    align-items: center;
    gap: 5px;

    &_info {
        color: var(--quinary-color) !important;
        @extend .regular-14;
    }
}

.info_text_comment {
    display: flex;

    flex: {
        direction: column;
        grow: 1
    }

    ;
    gap: 5px;
}