.image-gallery {
    display: flex;
    flex-direction: row;
    height: fit-content;
    gap: 10px;
}

.image-gallery img {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 15%;
}

.image-gallery img:last-child {
    margin-right: 0;
}

.plus-icon {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    height: fit-content;
    width: fit-content;
    color: var(--secondary-color);
}