.button-submit {
    width: 150px;
    height: 40px;
    border-radius: 15px;
    box-shadow: 2px 2px 4px 0px #00000033;
    background-color: var(--tertiary-color);
    color: var(--secondary-color);
    cursor: pointer;
    width: 100%;
}

.inactive {
    opacity: 50%;
    cursor: default !important;
}

.button-container {
    width: 100%;
}