.source_container {
    object-fit: cover;
    border: {
        radius: 25px;
    };
    width: 100%;
    height: 100%;
}

.arrow_icon {
    margin-left: 5px;
}

.play_icon {
    color: var(--secondary-color);
    width: 100px !important;
    height: 100px !important;
    position: absolute;
    bottom: 0;
    top: 0;
    margin: auto;
    right: 0;
    left: 0;
}

.icon_holder {
    height: 32px;
    width: 32px;
    background-color: var(--secondary-color);
    border-radius: 50%;
    display: grid;
    place-items: center;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    cursor: pointer;
    z-index: 9;
}

.right_arrow {
    @extend .icon_holder;
    right: 12px;
}

.left_arrow {
    @extend .icon_holder;
    left: 12px;
    transform: rotate(180deg);
}

.main {
    position: relative;
    width: 320px;
    height: 320px;
    overflow: hidden;
}

.forward {
    animation: appearFromLeft 1s ease forwards;
}

.backward {
    animation: appearFromRight 1s ease forwards;
}

@keyframes appearFromLeft {
    0% {
        opacity: 0;
        transform: translateX(-100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes appearFromRight {
    0% {
        opacity: 0;
        transform: translateX(100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

.actions-main-container {
    border-radius: 50%;
    display: flex;
    position: absolute;
    bottom: 20px;
    margin: auto 0;
    cursor: pointer;
    z-index: 9;
    right: 10px;
    flex-direction: row-reverse;
    gap: 20px;
}

.tag-container {
    height: 32px;
    width: 32px;
    background-color: var(--secondary-color);
    border-radius: 50%;
    place-items: center;
    margin: auto 0;
    cursor: pointer;
    z-index: 9;
    display: grid;
}

.location-container {
    height: 32px;
    width: 32px;
    background-color: var(--secondary-color);
    border-radius: 50%;
    display: grid;
    place-items: center;
    margin: auto 0;
    cursor: pointer;
    z-index: 9;
}

.song-container {
    height: 32px;
    width: 32px;
    background-color: var(--secondary-color);
    border-radius: 50%;
    display: grid;
    place-items: center;
    margin: auto 0;
    cursor: pointer;
    z-index: 9;
}
