@import '../../../../App.scss';

.cross_icon {
    color: var(--primary-color);
}

.header_view {
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--secondary-color);
    padding: 20px 12px;
    border-bottom: 0.5px solid var(--quinary-color);
    box-shadow: 0px 4px 4px 0px #1E1E1E26;
}

.location_text {
    @extend .regular-14;
    color: var(--quinary-color);
}

.repost_preview {
    display: block !important;

    .image_preview_container {
        height: 100%;   
    }

    .attachments {
        height: calc(100% - 70px);
        padding: 20px;

        .grid_2 {
            height: 100%;
        }
    }
    .main {
        height: 100% !important;
        width: 100% !important;
    }
}

.right_view {
    height: 100%;
    overflow: auto;
}