.segment-background {
    height: 40px;
    padding: 5px 8px;
    border-radius: 50px;
    border: 0.5px;
    background: var(--secondary-color);
    display: flex;
    align-items: center;
    box-shadow: 2px 2px 4px 0px #00000033;
}

.segment-button {
    width: auto;
    height: 40px;
    padding: 8px, 0px, 8px, 0px;
    border-radius: 50px;
    gap: 10px;
    background: var(--tertiary-color);
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    flex-grow: 1;
}

.segment_color {
    color: var(--quinary-color);
}

.segment-button-inactive {
    width: auto;
    height: 40px;
    padding: 8px, 0px, 8px, 0px;
    border-radius: 50px;
    gap: 10px;
    background: var(--secondary-color);
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    flex-grow: 1;
}

.segment-label {
    color: var(--secondary-color);
}

.segment-count-inactive {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: var(--tertiary-color);
    color: var(--secondary-color);
    display: flex;
    align-items: center;
    justify-content: center;
}

.segment-count {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: var(--secondary-color);
    color: var(--tertiary-color);
    display: flex;
    align-items: center;
    justify-content: center;
}